.gallery {
    height: 100%;
    position: relative;
}

.current-slide {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 142.6%;
    color: #FFFFFF;

    position: absolute;
    bottom: 25px;
    left: 50%;
    z-index: 1;
}

@media all and (max-width: 480px) {
    .gallery {
        height: 255px;
    }

    .current-slide {
        bottom: 0px;
        left: 47%;
    }
}